.some_class{
    color : white;
    margin-bottom: 10px;
}
.some_class label{
    margin: 0px 10px;
    font-size: 1.1rem;
}

.some_class select{
    border-radius: 4px;
    padding: 3px 8px;
    background-color: #eae9e9;
    /* font-size: 1.1rem; */
}

.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #c3b4f3;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.75rem;
  text-align: center;
}
.chart {
    padding: 1rem;
    border-radius: 12px;
    background-color: #f8dfff;
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 10rem;
    margin-bottom: 20px;
}
.form{
    margin-top: 20px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color : white;
}
.form input{
    margin: 2px 0;
    border-radius: 20px;
    padding: 4px 10px;
    background-color: #eae9e9;
    color: black;
    border: 2px solid rgb(39, 23, 23);
}
.form button{
    width: 100%;
    margin-top: 10px;
    border-radius: 10px;
    padding: 3px 10px;
    font-size: 1.2rem;
    background-color: #853d65;
    border: 2px solid #a58195;
    color : white;
    /* border: none; */
}
.form button:hover{
    background-color: #7b365c;
    color: rgb(223, 215, 215);
}

